@import url(https://fonts.googleapis.com/css?family=Arimo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

.logo{
  height: 100px;
  position: fixed;
  top: 0;
  right: 30px;
}

p, h2{
  color: #6C3A5C;
  
}

.result{
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 body {
	 display: flex;
   justify-content: center;
	background: linear-gradient(-45deg, #B5C9C3, #cfd8d7, #e7cfcd, #9f9aa4);
	background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  min-height: 1000px;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
 .wrapperCalculator {
	 margin-top: 5%;
	 display: flex;
	 justify-content: center;
	 font-family: "Arimo";
	 background-color: #B5C9C3;
	 box-shadow: 9px 13px 25px 0px rgba(0, 0, 0, 0.18);
	 animation: slideUp 2000ms ease;
}

.match{
  width:60%;
  min-width: 400px;
}
.matchContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

 @keyframes slideUp {
	 0% {
		 transform: translateY(100%);
		 visibility: visible;
	}
	 100% {
		 transform: translateY(0);
	}
}
 .container {
	 width: 65%;
	 padding: 5% 10%;
}
 h1 {
	 align-self: center;
}
 form {
	 width: 100%;
}
 form > * {
	 margin-top: 20px;
}
 form input {
	 width: 200px;
	 border: 0;
	 font-size: 1rem;
	 letter-spacing: 0.15rem;
	 font-family: "Arimo";
	 margin-bottom: 5px;
	 color: #6C3A5C;
   border-radius: 4px;
   margin-right: 30px;
   height: 26px;
}
form input[type=file]{
  width: 230px;
}
 form label {
	 text-transform: uppercase;
	 font-size: 11px;
	 letter-spacing: 2px;
   color:#6C3A5C;
   margin-right: 15px;
   width:150px;
}
 form h1 {
	 font-size: 24px;
	 line-height: 10px;
	 color: #493b76;
	 letter-spacing: 1px;
}
 form h1:nth-of-type(2) {
	 margin-top: 10%;
}
 .name {
	 justify-content: space-between;
	 display: flex;
	 width: 100%;
}

 .address-info {
	 display: flex;
	 justify-content: space-between;
}

 .cc-info {
	 display: flex;
	 justify-content: space-between;
}
 .cc-info div {
	 width: 45%;
}

select {
  position: relative;
  width: 200px;
  margin-right: 30px !important;
  font-family: 'Open Sans', 'Helvetica Neue', 'Segoe UI', 'Calibri', 'Arial', sans-serif;
  font-size: 18px;
  color: #60666d;
}

option {
  display: block;
  padding: 15px;
  background-color: #fff;
}
option:hover, option:focus {
  color: #546c84;
  background-color: #fbfbfb;
}
#roomsNumber {
  display: flex;
  flex-direction: row;
  justify-content: center;
  max-height: 30px;
  margin-right: 30px;
  width: 200px;
}
 .btns {
	 display: flex;
	 flex-direction: column;
	 align-items: flex-end;
}
 .btns  {
	 margin: 3px 0;
	 height: 30px;
	 color: #cfc9e1;
	 background-color: #4a3b76;
	 text-transform: uppercase;
	 border: 0;
	 border-radius: 0.3rem;
   letter-spacing: 2px;
   margin: 50px;
   display: inline
}
 .btns:hover {
	 animation-name: btn-hov;
	 animation-duration: 550ms;
	 animation-fill-mode: forwards;
}


 @keyframes btn-hov {
	 100% {
		 background-color: #cfc9e1;
		 color: #4a3b76;
		 transform: scale(1.05);
	}
}
 input:focus, button:focus {
	 outline: none;
}
 @media (max-width: 736px) {
  .container-flatlist {
    display: block;
    flex-direction: column
  }
	 .wrapperCalculator {
		 width: 100%;
	}
	 .container {
		 width: 100%;
	}
	 .btns {
		 align-items: center;
	}
	 .btns button {
		 width: 50%;
  }
  .roomsNumber {
    display: flex;
    flex-direction: row;
  }
	 form h1 {
		 text-align: center;
	}
	 .name, .address-info, .cc-info {
		 flex-direction: column;
		 width: 100%;
     justify-content: space-between;
     align-items: center;
	}
	 .name div, .address-info div, .cc-info div {
		 align-items: center;
		 flex-direction: column;
		 width: 100%;
		 display: flex;
	}
	 .street, .cc-num {
		 text-align: center;
	}
	 input {
		 margin: 5px 0;
		 max-height: 30px;
  }
 
}
 
